import React from 'react';
import {ICONS} from './icons.js'
import Button from '@material-ui/core/Button';
import './css/studentInfo.css'

export default function StudentInfo(props) {
  const {name, degree, year, token} = props.data
  const student = props.data

  return (
    <div className="student">

      <div className="data">
        <div className="student--name">{name}</div>
        <div className="student--info">
          <div className="student--year">{degree} – {year} курс</div>
        </div>
      </div>

      <div className="actions">
        <Button variant="flat" color="primary" onClick={() => props.onSubmit(student)}>обрати</Button>
      </div>

    </div>


    // <ul id="student-info" className="nobullet">
    //   { props.data.map(item => {
    //     return (
    //       <li key={item.name} className="result-item">
    //         {/*<p className="result-item-name">{item.name}</p><i className={ICONS[item.icon]}></i>*/}
    //         <h2 className="result-item-value">{item.value}</h2>
    //       </li>
    //     )
    //   })}
    // </ul>
  )
}



